<template>
  <!-- AddQna.vue -->
  <div class="submit-form">
    <!-- ÏÉà ÏñëÏãù Ìèº ÏãúÏûë -->
    <div v-if="!submitted">
      <div class="margin">
      <!-- ÏßàÎ¨∏ -->
      <div class="form-group">
        <label for="question">Question</label>
        <input
          type="text"
          class="form-control"
          id="question"
          required
          v-model="qna.question"
          name="question"
        />
      </div>
      
      <!-- ÎãµÎ≥Ä -->
      <div class="form-group">
        <label for="answer">Answer</label>
        <input
          type="text"
          class="form-control"
          id="answer"
          required
          v-model="qna.answer"
          name="answer"
        />
      </div>
      
      <!-- ÏßàÎ¨∏Ïûê -->
      <div class="form-group">
        <label for="questioner">Questioner</label>
        <input
          type="text"
          class="form-control"
          id="questioner"
          required
          v-model="qna.questioner"
          name="questioner"
        />
      </div>

      <!-- ÎãµÎ≥ÄÏûê -->
      <div class="form-group">
        <label for="answerer">Answerer</label>
        <input
          class="form-control"
          id="answerer"
          required
          v-model="qna.answerer"
          name="answerer"
        />
      </div>
    </div>
      <!-- ÏÉàÎ°úÏö¥ Í≥†Í∞ùÏ†ïÎ≥¥ ÏûëÏÑ± ÌõÑ Ï†úÏ∂úÎ≤ÑÌäº -->
      <button @click="saveQna" class="btn btn-success">Submit</button>
    </div>
    <!-- ÏÉà ÏñëÏãù Ìèº ÎÅù -->
    
    <!-- ÏïΩÍ∞Ñ ÎîîÏûêÏù∏ Í∞úÏÑ†Ìï® -->
    <div v-else>
      <div class="alert alert-success" role="alert">
        Save qna successfully!
      </div>
      <!-- Ï∂îÍ∞Ä Îì±Î°ù Î≤ÑÌäº -->
      <button @click="newQna" class="btn btn-primary">
        Add New Qna
      </button>
    </div>
  </div>

</template>

<script>
// üëÄaxios Í≥µÌÜµÌï®Ïàò(springboot Ïó∞ÎèôÏùÑ ÏúÑÌïú Ìï®ÏàòÎì§Ïùò Î™®ÏûÑ) import ÌïÑÏàò!!
import QnaDataService from "../../services/QnaDataService";

export default {
  data() {
    return {
      qna: {
        question: "",     //string ÏùÄ Ï¥àÍ∏∞Í∞íÏÑ∏ÌåÖ "" (int Îäî null)
        answer: "",
        questioner: "",
        answerer: "",
      },
      // submit Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠ÌïòÎ©¥ true(Î∞±Ïóî, insert)Í∞Ä ÎêòÍ≥†, You submitted successfully! ÌôîÎ©¥Ïóê Ï∂úÎ†•Îê®
      submitted: false
    };
  },
  methods: {
    saveQna() {
      // ÏûÑÏãú Í∞ùÏ≤¥ Î≥ÄÏàò -> springboot Ï†ÑÏÜ°
      // Î≤àÌò∏Îäî(cid) ÏûêÎèôÏÉùÏÑ±ÎêòÎØÄÎ°ú ÎπºÍ≥† Ï†ÑÏÜ°Ìï®
      let data = {
        question: this.qna.question,
        answer: this.qna.answer,
        questioner: this.qna.questioner,
        answerer: this.qna.answerer,
      };

      // insert ÏöîÏ≤≠ Ìï®Ïàò Ìò∏Ï∂ú(axios Í≥µÌÜµÌï®Ïàò Ìò∏Ï∂ú)
      QnaDataService.create(data)
      // ÏÑ±Í≥µÌïòÎ©¥ then() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
      .then(response => {
        this.qna.cid = response.data.cid;
        // ÏΩòÏÜî Î°úÍ∑∏ Ï∂úÎ†•(response.data)
        console.log(response.data);
        // Î≥ÄÏàò submitted 
        this.submitted = true;
      })
      // Ïã§Ìå®ÌïòÎ©¥ .catch() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
      .catch(e => {
        console.log(e);
      })

    },
    newQna() {
      // ÏÉàÏñëÏãù Îã§Ïãú Î≥¥Ïó¨Ï£ºÍ∏∞ Ìï®Ïàò, Î≥ÄÏàò Ï¥àÍ∏∞Ìôî (Í≥†Í∞ùÎì±Î°ù ÌõÑ Ï∂îÍ∞ÄÎ°ú Îòê Í≥†Í∞ù Îì±Î°ùÌï† Îïå)
      this.submitted = false;
      this.qna = {}
    }
  },
};
</script>

<style>
  .submit-form {
    max-width: 300px;
    margin: auto;
